/* About Us */
.about-us-content,
.team-content {
  padding: 40px 0;
  width: 95%;
  margin: auto;
  margin-bottom: 20px;
  background: #fff;
  margin-top: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.about-us-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-us-content img {
  background: #eee;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  width: 500px;
  height: 500px;
  height: auto;
  border: none;
  border-radius: 20px;
}

.about-us-content > .DeskripsiTentang {
  width: 100%;
  max-width: 800px;
  text-align: center;
}

.about-us-content h2,
.team-content h2 {
  color: #00adb5;
  font-size: 2rem;
  margin-bottom: 20px;
}

.about-us-content p {
  text-align: justify;
  font-size: 1.1rem;
  color: #333;
}

.team-content p {
  text-align: justify;
  font-size: 1.1rem;
  color: #333;
  padding: 0px 8%;
}

.team-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.team-card {
  width: 100%;
  max-width: 300px;
  box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
  background-color: #eee;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-card:hover {
  transform: translateY(-10px);
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.5);
}

.team-card img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  display: block;
  margin: 0 auto 15px;
}

.team-card h3 {
  color: #ebb22f;
  font-size: 1.2em;
  margin: 10px 0;
  text-align: center;
}

.team-card p {
  color: #333;
  margin: 10px 0;
  text-align: center;
  font-weight: bold;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 10px;
}

.social-icons a {
  color: #00adb5;
  font-size: 1.5rem;
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #ffdd57;
}

/* Footer Section */
.footer-section {
  background-color: #393e46;
  color: #fff;
  padding: 40px 20px;
}

.footer-section .footer-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.footer-section .footer-top > .footer-top-info {
  display: flex;
  justify-content: center;
}

.footer-section .footer-logo img {
  max-width: 100px;
  display: flex;
  align-items: center;
}

.footer-section .footer-followUs,
.footer-section .footer-legal {
  flex: 1;
  margin: 10px;
}

.footer-section .footer-followUs h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  padding-right: 20px;
}
.footer-section .footer-legal h2 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.footer-section .footer-followUs ul {
  list-style: none;
  padding: 0;
  padding-left: 25px;
}

.footer-section .footer-legal ul {
  list-style: none;
  padding: 0;
  padding-left: 5px;
}

.footer-section .footer-followUs ul li,
.footer-section .footer-legal ul li {
  margin-bottom: 10px;
}

.footer-section .footer-followUs ul li a,
.footer-section .footer-legal ul li a {
  color: #00adb5;
  text-decoration: none;
  transition: color 0.3s;
  font-size: 0.9rem;
}

.footer-section .footer-followUs ul li a:hover,
.footer-section .footer-legal ul li a:hover {
  color: #ebb22f;
}

.footer-section .footer-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #555;
  padding-top: 20px;
}

.footer-section .footer-copyright {
  font-size: 1rem;
  display: flex;
  align-items: center;
  color: #a5a1a1;
}

.footer-section .footer-sosmed {
  display: flex;
  gap: 10px;
}

.footer-section .footer-sosmed i {
  font-size: 1.5rem;
  color: #00adb5;
  transition: color 0.3s;
  cursor: pointer;
}

.footer-section .footer-sosmed i:hover {
  color: #ebb22f;
}

.footer-section.active {
  filter: blur(15px);
}

/* My swal */
.my-swal {
  width: 400px;
  height: 300px;
}

.my-swal .swal2-popup {
  font-size: 1.2em;
}

/* Skip to Content */
.skip-to-content {
  font-family: "Nunito Sans", sans-serif;
  position: absolute;
  top: -60px;
  left: 0;
  background-color: #ebb22f;
  color: white;
  padding: 8px;
  min-width: 44px;
  min-height: 44px;
  z-index: 9999;
}

.skip-to-content:focus {
  top: 0;
