der-radius: 10px;\r\n  cursor: pointer;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  font-weight: 700;\r\n}\r\n\r\n.modal-content > #confirmLogoutButton {\r\n  background: transparent;\r\n  border-color: #00adb5;\r\n  color: #00adb5;\r\n}\r\n\r\n.modal-content > #confirmLogoutButton:hover {\r\n  background: #00adb5;\r\n  border-color: #0c8086;\r\n  color: #fff;\r\n}\r\n\r\n.modal-content > #cancelLogoutButton {\r\n  background: #00adb5;\r\n  border-color: #00adb5;\r\n  color: #fff;\r\n}\r\n\r\n.modal-content > #cancelLogoutButton:hover {\r\n  background: #04858b;\r\n  border-color: #04858b;\r\n}\r\n\r\n/* Profile */\r\n.nav-profile {\r\n  display: block;\r\n  padding-top: 10px;\r\n}\r\n\r\n.profile-dropdown {\r\n  display: inline-block;\r\n}\r\n\r\n.profile-picture {\r\n  width: 44px;\r\n  height: 44px;\r\n  border-radius: 50%;\r\n  cursor: pointer;\r\n}\r\n\r\n.dropdown-menu {\r\n  display: none;\r\n  position: absolute;\r\n  top: 60px;\r\n  margin-right: 0;\r\n  background-color: white;\r\n  min-width: 150px;\r\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\r\n  z-index: 1;\r\n}\r\n\r\n.profile-dropdown:hover .dropdown-menu {\r\n  display: block;\r\n}\r\n\r\n.dropdown-menu li {\r\n  padding: 12px 16px;\r\n  border-bottom: 1px solid #ddd;\r\n  list-style: none;\r\n}\r\n\r\n.dropdown-menu li a,\r\n.dropdown-menu li button {\r\n  color: black;\r\n  text-decoration: none;\r\n  display: block;\r\n  width: 100%;\r\n  background: none;\r\n  border: none;\r\n  text-align: left;\r\n  cursor: pointer;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.dropdown-menu li:hover {\r\n  background-color: #f1f1f1;\r\n}\r\n\r\n.profile-picture {\r\n  width: 44px;\r\n  height: 44px;\r\n  border-radius: 50%;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.form-group {\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.form-group label {\r\n  display: block;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.form-group input {\r\n  width: 100%;\r\n  padding: 8px;\r\n  box-sizing: border-box;\r\n}\r\n\r\n.btn {\r\n  padding: 10px 20px;\r\n  border: none;\r\n  cursor: pointer;\r\n}\r\n\r\n.btn-primary {\r\n  background-color: #007bff;\r\n  color: white;\r\n}\r\n\r\n.btn-secondary {\r\n  background-color: #6c757d;\r\n  color: white;\r\n}\r\n\r\n.btn + .btn {\r\n  margin-left: 10px;\r\n}\r\n\r\n/* Profile Detail */\r\n.profile-card {\r\n  background: #eee;\r\n  border-radius: 8px;\r\n  padding: 20px;\r\n  width: 100%;\r\n\r\n  text-align: center;\r\n}\r\n\r\n.profile-title {\r\n  font-size: 24px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.profile-form {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  flex-direction: column;\r\n}\r\n\r\n.profile-form > .form-group {\r\n  margin-bottom: 15px;\r\n  text-align: left;\r\n  width: 30%;\r\n}\r\n\r\n.profile-form > .form-group label {\r\n  display: block;\r\n  margin-bottom: 5px;\r\n  font-weight: bold;\r\n}\r\n\r\n.profile-form > .form-group input {\r\n  width: 100%;\r\n  padding: 10px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 4px;\r\n  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.profile-form > .submit-button {\r\n  background: #00adb5;\r\n  color: #fff;\r\n  padding: 10px;\r\n  border: none;\r\n  border-radius: 20px;\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  cursor: pointer;\r\n  width: 30%;\r\n}\r\n\r\n.profile-form > .submit-button:hover {\r\n  background: #04858b;\r\n}\r\n\r\n.error-message {\r\n  color: red;\r\n  font-weight: bold;\r\n}\r\n\r\n/* Humberg Button */\r\nbutton.hamburger-menu {\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  padding: 10px;\r\n  background: none;\r\n  border: none;\r\n  cursor: pointer;\r\n  display: none;\r\n  z-index: 99;\r\n}\r\n\r\nbutton.hamburger-menu > i {\r\n  color: #333;\r\n  font-size: 1.5rem;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\nbutton.hamburger-menu > i.icon-close {\r\n  display: none;\r\n}\r\n\r\n/* Hero Section */\r\n.hero-section {\r\n  display: flex;\r\n  align-items: center;\r\n  height: 90vh;\r\n  width: 100%;\r\n}\r\n\r\n.hero-section > .hero-description {\r\n  width: 50%;\r\n  padding-left: 40px;\r\n}\r\n\r\n.hero-section > .hero-description > h1 {\r\n  font-family: \"kanit\", sans-serif;\r\n  font-size: 2rem;\r\n  color: #ebb22f;\r\n}\r\n\r\n.hero-section > .hero-description > h1 > span {\r\n  font-family: \"kanit\", sans-serif;\r\n  font-size: 2rem;\r\n  color: #00adb5;\r\n}\r\n\r\n.hero-section > .hero-description > p {\r\n  padding-top: 10px;\r\n  padding-bottom: 10px;\r\n}\r\n\r\n.hero-section > .hero-description > a {\r\n  min-height: 44px;\r\n  min-width: 44px;\r\n  text-decoration: none;\r\n  color: #333;\r\n}\r\n\r\n.hero-section > .hero-description > .button {\r\n  border: 2px solid #ff9710;\r\n  background-color: transparent;\r\n  padding: 6px 7px 6px 10px;\r\n  border-radius: 10px;\r\n  transition: 0.3;\r\n  text-align: center;\r\n  font-family: \"Kanit\", sans-serif;\r\n  display: inline-block;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  font-weight: bold;\r\n}\r\n\r\n.hero-section > .hero-description > .button:hover {\r\n  background-color: #ffce10;\r\n  transform: scale(1.1);\r\n}\r\n\r\n.hero-section > .hero-image {\r\n  display: flex;\r\n  justify-content: center;\r\n  padding-top: 20px;\r\n  width: 50%;\r\n}\r\n\r\n/* Test Introduction */\r\n.test-introduction-section {\r\n  display: flex;\r\n  align-items: center;\r\n  background-color: #393e46;\r\n  width: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n.test-introduction-section > .test-description {\r\n  width: 50%;\r\n  padding-left: 40px;\r\n}\r\n\r\n.test-introduction-section > .test-description > h1 {\r\n  font-family: \"kanit\", sans-serif;\r\n  font-size: 2rem;\r\n  color: #ebb22f;\r\n}\r\n\r\n.test-introduction-section > .test-description > h1 > span {\r\n  font-family: \"kanit\", sans-serif;\r\n  font-size: 2rem;\r\n  color: #00adb5;\r\n}\r\n\r\n.test-introduction-section > .test-description > p {\r\n  padding-top: 10px;\r\n  padding-bottom: 10px;\r\n  color: #eee;\r\n}\r\n\r\n.test-introduction-section > .test-image {\r\n  display: flex;\r\n  justify-content: center;\r\n  padding-top: 20px;\r\n  width: 50%;\r\n}\r\n\r\n/* FAQ */\r\n.faq-section {\r\n  max-width: 800px;\r\n  margin: 40px auto;\r\n  padding: 20px;\r\n  background-color: #fff;\r\n  border-radius: 10px;\r\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.faq-section h2 {\r\n  text-align: center;\r\n  margin-bottom: 20px;\r\n  font-size: 2rem;\r\n  color: #393e46;\r\n}\r\n\r\n.faq-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 15px;\r\n}\r\n\r\n.faq-item {\r\n  background-color: #f9f9f9;\r\n  border-radius: 5px;\r\n  padding: 15px;\r\n  transition: background-color 0.3s ease;\r\n}\r\n\r\n.faq-item:hover {\r\n  background-color: #e0e0e0;\r\n}\r\n\r\n.faq-item .question {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  cursor: pointer;\r\n  font-size: 1.1rem;\r\n  color: #333;\r\n}\r\n\r\n.faq-item .question h3 {\r\n  margin: 0;\r\n}\r\n\r\n.faq-item .question button.toggle-icon {\r\n  font-size: 1.5rem;\r\n  transition: transform 0.3s ease;\r\n  border: none;\r\n  background: transparent;\r\n}\r\n\r\n.faq-item .answer {\r\n  display: none;\r\n  padding: 10px 0 0;\r\n  font-size: 1rem;\r\n  color: #555;\r\n}\r\n\r\n.faq-item.active .answer {\r\n  display: block;\r\n}\r\n\r\n.faq-item.active .question .toggle-icon {\r\n  transform: rotate(45deg);\r\n}\r\n\r\n/* Test Section */\r\n.main {\r\n  min-height: 60vh;\r\n  width: 100%;\r\n  background-size: cover;\r\n  background-position: center;\r\n  pointer-events: auto;\r\n  transition: 0.3s ease;\r\n}\r\n\r\nsection.test-entry {\r\n  position: relative;\r\n  height: 85vh;\r\n  width: 100%;\r\n}\r\n\r\n.test-entry > .test-entry-content {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  height: 85vh;\r\n}\r\n\r\n.test-entry-content > h1 {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  