___CSS_LOADER_EXPORT___.push([module.id, `@media only screen and (max-width: 1200px) {
  .popup-info {
    width: 450px;
  }
  .test-section .test-box {
    width: 450px;
  }

  .test-box .question-text {
    font-size: 20px;
  }

  .test-header span {
    font-size: 17px;
  }

  .test-section .result-box {
    width: 450px;
  }
}

@media only screen and (max-width: 1035px) {
  /* login */
  .login > .login-form > p,
  .login > .login-form > p > a {
    font-size: 0.9rem;
  }

  .profile-form > .form-group {
    width: 40%;
  }
}

@media only screen and (max-width: 982px) {
  /* navbar */
  .wrapper > .nav-menu {
    width: 80%;
    padding-top: 5px;
  }

  .wrapper > .nav-menu > ul > li {
    display: inline;
    padding: 0;
  }

  .wrapper > .nav-menu > ul > li > a {
    display: inline-block;
    padding: 10px;
    font-size: 0.9rem;
  }

  /* Home */
  .hero-section {
    height: 85vh;
  }

  .hero-section > .hero-description {
    width: 50%;
    padding-left: 40px;
  }

  .hero-section > .hero-description > h1 {
    font-size: 1.5rem;
  }

  .hero-section > .hero-description > h1 > span {
    font-size: 1.5rem;
  }

  .hero-section > .hero-description > p {
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: justify;
    font-size: 0.9rem;
  }

  .hero-section > .hero-image img {
    width: 90%;
  }

  .test-introduction-section > .test-description > h1 {
    font-size: 1.5rem;
  }

  .test-introduction-section > .test-description > p {
    font-size: 0.8rem;
    text-align: justify;
    padding-right: 15px;
  }

  .test-introduction-section > .test-description > h1 > span {
    font-size: 1.5rem;
  }

  .test-introduction-section > .test-image img {
    width: 90%;
  }

  .profile-form > .form-group {
    width: 50%;
  }

  /* Register */
  .register > .register-form > p,
  .register > .register-form > p > a {
    font-size: 0.9rem;
  }

  /* About Us */
  .about-us-content img {
    width: 400px;
    height: 400px;
  }
}

@media only screen and (max-width: 850px) {
  /* Navbar */
  .nav-side > .nav-login {
    display: flex;
  }

  .nav-side > .nav-profie {
    display: flex;
    padding-top: 10px;
  }

  .wrapper > .nav-side {
    display: flex;
    justify-content: space-evenly;
    width: 30%;
  }

  button.hamburger-menu {
    display: flex;
    padding-top: 15px;
  }

  .tampil > ul {
    flex-direction: column;
    background-color: #333;
    width: 100%;
    position: absolute;
    top: 100px;
    left: 0;
  }

  .wrapper > .nav-menu {
    display: none;
  }

  .wrapper > .tampil {
    display: inline-block;
    color: #fff;
  }

  .nav-menu.tampil > ul {
    z-index: 9999;
  }

  .nav-menu.tampil > ul > li {
    margin-top: 10px;
    padding-top: 20px;
    min-width: 44px;
    min-height: 44px;
    width: 100%;
    z-index: 1000;
  }

  .nav-menu.tampil > ul > li > a {
    display: block;
    color: #fff;
  }

  /* Register */
  .register > .register-image {
    display: none;
  }

  .register > .register-form {
    width: 100%;
  }

  .register-image-buttonBack > a {
    border: 2px solid #eeeeee;
    color: #eee;
  }

  .register-image-buttonBack > a:hover {
    background-color: #eee;
    color: #ebb22f;
  }

  .register > .register-form > p {
    width: 60%;
  }

  /* Login */
  .login > .login-image {
    display: none;
  }

  .login > .login-form {
    width: 100%;
  }

  .login-image-buttonBack > a {
    border: 2px solid #eeeeee;
    color: #eee;
  }

  .login-image-buttonBack > a:hover {
    background-color: #eee;
    color: #ebb22f;
  }

  .login > .login-form > p {
    width: 60%;
  }

  /* Test Section */
  .wrapper > .test-entry > .test-entry-content > p {
    width: 80%;
    font-size: 1.3rem;
    display: flex;
    justify-content: center;
  }

  .profile-form > .form-group {
    width: 45%;
  }

  /* About Us */
  .about-us-content img {
    width: 300px;
    height: 300px;
  }

  .about-us-content > .DeskripsiTentang {
    width: 80%;
  }
}

@media only screen and (max-width: 750px) {
  /* Home */
  .hero-section {
    height: 75vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .hero-section > .hero-image {
    display: none;
  }

  .hero-section > .hero-description {
    width: 80%;
  }

  .test-introduction-section > .test-image {
    display: none;
  }

  .test-introduction-section {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30vh;
  }

  .test-introduction-section > .test-description {
    width: 80%;
  }

  .profile-form > .form-group {
    width: 50%;
  }

  .profile-form > .submit-button {
    width: 40%;
  }

  .test-section {
    height: 160%;
  }

  /* Footer */
  .footer-section .footer-top {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-section .footer-followUs,
  .footer-section .footer-legal {
    margin: 20px 0;
  }

  .footer-section .footer-bottom {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-section .footer-sosmed {
    margin-top: 10px;
  }

  /* About Us */
  .about-us-content,
  .team-content {
    padding: 20px;
  }

  .about-us-content img {
    width: 200px;
    height: 200px;
  }

  .team-card {
    flex: 1 0 calc(50% - 20px);
  }
}

@media only screen and (max-width: 600px) {
  /* Nav */
  .wrapper > .nav_login {
    width: 40%;
  }

  .wrapper > .nav-side {
    width: 40%;
  }

  .popup-info {
    width: 300px;
  }

  .popup-info h2{
    text-align: center;
    font-size: 1.5rem;
  }

  .popup-info .btn-group {
    display: flex;
    flex-direction: column;
  }

  .popup-info .btn-group .info-btn.continue-btn {
    margin-top: 10px;
  }

  .test-section .test-box {
    width: 300px;
  }

  .test-box .question-text {
    font-size: 15px;
  }

  .option-list .option span {
    font-size: 12px;
  }

  .test-section .result-box {
    width: 300px;
  }

  .test-section .result-box h2 {
    font-size: 40px;
  }

  .percentage-container .circular-progress::before {
    width: 200px;
    height: 200px;
  }

  .percentage-container .circular-progress {
    width: 230px;
    height: 230px;
  }

  .percentage-container .score-text {
    font-size: 20px;
  }

  .result-box > .feedback-container {
    font-size: 20px;
  }

  .result-box > .buttons {
    display: flex;
    flex-direction: column;
  }
}


@media only screen and (max-width: 425px) {
  /* Nav */
  .wrapper > .nav_login {
    width: 50%;
  }

  .wrapper > .nav-side {
    width: 50%;
  }

  .profile-form > .form-group {
    width: 65%;
  }

  .profile-form > .submit-button {
    width: 55%;
  }

  /* About Us */
  .team-card {
    flex: 1 0 calc(100% - 20px);
    margin: 10px 0;
  }
}
